// src/components/JobDescriptionModal.js
import { Icon } from "@iconify/react";

export default function JobDescriptionModal({ isOpen, onClose, onProceed, selectedOpening }) {
    // Sample job descriptions (move to a separate data file or API later)
    const jobDescriptions = {
        "Business Development Internship": {
            title: "Business Development Internship",
            description: "Join our dynamic team as a Business Development Intern! You’ll assist in identifying new business opportunities, building client relationships, and supporting strategic growth initiatives across African markets. Ideal candidates are proactive, creative, and eager to learn.",
            requirements: "Current enrollment in a business-related degree, strong communication skills, interest in African markets.",
        }
    };

    if (!isOpen || !selectedOpening) return null;

    const { title, description, requirements } = jobDescriptions[selectedOpening] || {};

    return (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50">
            <div className="bg-white rounded-lg p-6 max-w-lg w-full mx-4 shadow-xl animate-fade-in">
                <div className="flex items-center mb-4">
                    <Icon icon="mdi:briefcase" className="w-6 h-6 text-[#f05d23] mr-2" />
                    <h2 className="text-2xl font-bold text-[#231812]">{title}</h2>
                </div>
                <p className="text-[#231812] mb-4">{description}</p>
                <p className="text-[#231812] mb-6">
                    <strong>Requirements:</strong> {requirements}
                </p>
                <div className="flex justify-end gap-4">
                    <button
                        onClick={onClose}
                        className="px-4 py-2 bg-gray-300 text-[#231812] rounded-lg hover:bg-gray-400 transition duration-200"
                    >
                        Cancel
                    </button>
                    <button
                        onClick={onProceed}
                        className="px-4 py-2 bg-[#ff5d38] text-white rounded-lg hover:bg-[#d94f1e] transition duration-200 flex items-center"
                    >
                        Proceed
                        <Icon icon="mdi:arrow-right" width={20} height={20} className="ml-2" />
                    </button>
                </div>
            </div>
        </div>
    );
}